@import url('./Common.css');

#project{
    width: 100%; height: 100%;
    margin-top: 61px;
    background-color: #fff;
}

#promain{
  width: 100%; height:100vh;
  position: relative;
}
#promain::before{
  content: '';
  width: 0%;
  border-bottom: 2px solid #F93700;
  position: absolute;
  right: 0; top:21%;
  animation: pline 1s ease-in-out forwards;
  z-index: 5;
}
@keyframes pline{
  100%{width: 75%;}
}
#promain h1{
  color: #F93700;
  font-size: 5em;
  text-align: end;

  position: absolute;
  left: 9%; top: 25%;
  transform: rotate(270deg) translateY(100%);
  transform-origin: 0 100%;
  z-index: 5;
}
#plist{
  position: absolute;
  left: 25%; top: 17%;
  z-index: 5;
}
#plist span{
  position: relative;
  display: inline-block;
  font-weight: 600;
  color: #F93700;
  font-size: 1.2em;
  letter-spacing:5px;
  animation: listmove 1.8s ease-in-out infinite;
}
@keyframes listmove{
  0%{top: 0;}
  20%{top: -0.2em;}
  40%{top: 0;}
  60%{top: 0;}
  80%{top: 0;}
  100%{top: 0;}
}
#plist span:nth-of-type(1){
  animation-delay: 0.1s;
}
#plist span:nth-of-type(2){
  animation-delay: 0.2s;
}
#plist span:nth-of-type(3){
  animation-delay: 0.3s;
}
#plist span:nth-of-type(4){
  animation-delay: 0.4s;
}
#plist span:nth-of-type(5){
  animation-delay: 0.5s;
}
#plist span:nth-of-type(6){
  animation-delay: 0.6s;
}
#plist span:nth-of-type(7){
  animation-delay: 0.7s;
}
#plist span:nth-of-type(8){
  animation-delay: 0.8s;
}
#plist span:nth-of-type(9){
  animation-delay: 0.9s;
}
#plist span:nth-of-type(10){
  animation-delay: 1s;
}


#pmain_detail{
  width: 75%;
  margin-left: 25%;
  padding-top: 200px;
  display: flex;
  overflow: hidden;
}

#pvideo{
  width: 60%; height:400px;
  border: 15px solid #eee;
  border-radius: 30px;
  box-sizing: initial;
  overflow: hidden;
} 
#pvideo video{
  width: 100%; height: 100%;
  object-fit: fill;
}

#ptext{
  width: 35%;
  text-align: end;
  padding:200px 40px 0 0;
  word-break: keep-all;
}
#psite{
  width: 150px; height: 30px;
  background-color: #DBD5C9;
  border-radius: 15px;
  margin-left: 210px;
}
#psite span{
  padding: 5px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}
#psite span a{
  color: #F93700;
}
#psite span a:hover{
  color: #fff;
}
#ptext h3{
  color: #F93700;
  font-size: 1.6em;
  padding: 30px 0 15px;
}
#ptext p{
  font-family: sans-serif;
  font-size: 0.8em;
  line-height: 2;
  word-break: keep-all;
  color: #919191;
}

#pmain_con{
  width: 100%; height: 100vh;
  background-color: #DBD5C9;
  padding: 50px 0;
  position: relative;
}
#pcon_tit{
  width: 80%;
  margin: 0 auto;
}
#pcon_tit h3::before{
  content: '';
  display: inline-block;
  width: 100%;
  border-top: 1px solid floralwhite;
  border-bottom: 1px solid floralwhite;
}
#pcon_tit h3{
  text-align: center;
  font-size: 1.5em;
  text-transform: uppercase;
  padding-bottom: 70px;
  color: #F93700;
}
#pcon_tit h3::after{
  content: '';
  display: inline-block;
  width: 100%;
  border-top: 1px solid floralwhite;
  border-bottom: 1px solid floralwhite;
}
#pcon_content{
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

#pcon_video{
  border: 10px solid #eee;
  border-radius: 30px;
  box-sizing: initial;
  overflow: hidden;
  width: 60%; height: 400px;
}
#pcon_video video{
  width: 100%; height: 100%;
  object-fit: cover;
}
#pmain_con ul{
  width: 35%;
  margin-top: 40px;
  text-align: end;
  padding: 0;
}
#pmain_con ul li{
  padding: 15px 0;
}
#pmain_con ul li p{
  color: #F93700;
  font-size: 1em;
  text-align: start;
  font-weight: 600;
}
#pmain_con ul li p::after{
  content: '';
  width: 100%;
  padding: 3px 0;
  border-bottom: 1px dashed #fff;
  display: block;
}
#pmain_con ul li span{
  text-align: end;
  font-family: sans-serif;
  font-size: 1em;
  line-height: 2;
  color: #fff;
  font-weight: 600;
}

#psub_con{
  width: 100%; height: 100vh;
}
#psub_tit{
  width: 60%;
  margin: 0 auto;
  padding: 100px 0;
  overflow: hidden;
}

#psub_tit h2{
  font-size: 6em;
  animation: subtext 1s forwards;
  animation-delay:2s;
  transform: translateX(-100%);
}
#psub_tit h2:nth-of-type(1):after{
  content: '';
  display: inline-block;
  border-bottom: 2px solid #F93700;
  width: 54%;
}
@keyframes subtext{
  100%{transform: translateX(0%);}
}
#psub_tit h3{
  text-align: center;
  font-size: 8em;
}
#psub_tit h3 span{
  position: relative;
  display: inline-block;
  animation: subtext2 2s ease-in-out infinite;
}
@keyframes subtext2{
  0%{top: 0;}
  20%{top: -0.2em;}
  40%{top: 0;}
  60%{top: 0;}
  80%{top: 0;}
  100%{top: 0;}
}
#psub_tit h3 span:nth-of-type(1){
  animation-delay: 0.1s;
}
#psub_tit h3 span:nth-of-type(2){
  animation-delay: 0.2s;
}
#psub_tit h3 span:nth-of-type(3){
  animation-delay: 0.3s;
}
#psub_tit h3 span:nth-of-type(4){
  animation-delay: 0.4s;
}
#psub_tit h3 span:nth-of-type(5){
  animation-delay: 0.5s;
}
#psub_tit h3 a{
  color: #DBD5C9;
}

#psub_tit h2:nth-of-type(2)::before{
  content: '';
  display: inline-block;
  border-bottom: 2px solid #F93700;
  width: 43%;
}
#psub_tit h2:nth-of-type(2){
  text-align: end;
  animation: subtext 1s forwards;
  animation-delay:3s;
  transform: translateX(100%);
  padding-top:20px;
}
#psub_tit h2 a{
  color: #F93700;
}

#psubimg{
  width: 100%; height: 100%;
  padding: 100px 0 50px;
  background-color: #F93700;
}
#psubgro{
  width: 90%;
  margin: 0 auto;
  padding: 50px 0 150px;
}
#psubgro p{
  font-weight: 700;
  color: #fff;
  font-size: 2em;
  text-align: center;
  padding-bottom: 50px;
  margin-bottom: 60px;
}

#psubgro p::after{
  content: '';
  display: block;
  width: 15%;
  margin: 0 auto;
  padding-top: 10px;
  border-bottom: 2px solid #fff;
}

#p_img{
  width: 100%;
  padding: 0;
  text-align: center;
}
#p_img li{
  width: 60%;
}
#p_img li:nth-of-type(2){
  margin-left: 40%;
  padding-top:120px;
}
#p_img li div{
  width: 100%; height: 500px;
  border: 15px solid #eee;
  border-radius: 30px;
  box-sizing: border-box;
  overflow: scroll;
  margin-bottom: 30px;
  font-size: 0;
  background-color: #fff;
}
#p_img li div::-webkit-scrollbar{
  width: 10px; height: 0px;
  /* display: none; */
}
#p_img li div::-webkit-scrollbar-thumb{
  border-radius: 15px;
}
#p_img li div::-webkit-scrollbar-track{
  background-color: #ddd;
  border-radius: 15px;
}
#p_img li img{
  width: 100%; 
  transition: 2s;
}
#p_img li span{
  color: #fff;
  font-size: 0.8em;
  word-break: keep-all;
  line-height: 1.8;
}


