*{
    box-sizing: border-box;
    font-family: 'Source Serif Pro', serif, 'Noto Sans KR', sans-serif;
  }
  ::-webkit-scrollbar{width: 5px;}
  ::-webkit-scrollbar-track{background-color: #eee;}
  ::-webkit-scrollbar-thumb{background-color: #F93700;}
  
  a{text-decoration: none;}
  ul,li{margin: 0; list-style: none;}
  html,body{
    margin: 0; padding:0;
    scroll-behavior: smooth;
  }
  h1,h2,h3,h4,p,span{margin: 0;cursor: default}
  html{
    width: 100%;
    position: relative;
  }
  body{
  background-color: #DBD5C9;
  }
  header{
  background-color: #DBD5C9;
  width: 100%;
  position: fixed;
  top: 0;
  border-bottom: 1px solid #fff;
  z-index: 15;
  }
  


  #head{
    width: 85%;
    margin: 0 auto;

    display: flex;
    justify-content: space-between;
  }
  #navlist{
    display: flex;
    padding: 0;
  }
  #navlist li{
    padding: 20px 40px;
  }
  #navlist li.active a{
    -webkit-text-decoration: line-through solid 2px #ffff;
            text-decoration: line-through solid 2px #ffff;
