    color: #F93700;
    font-size:1em;
    font-weight: 800;
  }
  #navlist a:hover{
    -webkit-text-decoration: line-through solid 2px #ffff;
            text-decoration: line-through solid 2px #ffff;
    color: #fff;
  }
  #navIcon ul{
    display: flex;
    padding: 0;
  }
  #navIcon ul li{
    padding: 18px 15px;
  }
  #navIcon ul li a{
    color: #F93700;
  }
  #navIcon ul li a:hover{
    color:#fff;
  }

