    #head{
      width: 100%;
    }
    #nav{
      width: 80%;
      margin:0 auto;
    }
    #navlist{
      width: 100%;
      justify-content: space-between;
    }
    #navlist li{
      padding: 20px;
    }
    #navlist a{
      color: #F93700;
      font-size:0.8em;
      font-weight: 800;
    }
