#main{
  width: 100%; height: 100%;
  margin-top:62px;
  padding: 40px 0;
  overflow: hidden;
  position: relative;
}

#maintext{
  position: relative;
  width: 100%;
  padding: 20px 0;
}
#svgname{
  width:10%;
  position: absolute;
  left:38%; top: 0%;
  -webkit-animation: namemove 1.2s linear forwards;
          animation: namemove 1.2s linear forwards;
}
@-webkit-keyframes namemove{
  100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}
@keyframes namemove{
  100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}
#hello{
  color: #fff;
  font-size:9em;
  margin-left: 200px;
  padding: 20px 0;
  -webkit-animation: hello 1s;
          animation: hello 1s;
}
#hello::selection{
  background-color: #F93700;
  color: #fff;
}
@-webkit-keyframes hello {
  0%{
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
    -webkit-text-stroke:3px #fff;
    color: #DBD5C9;
  }

  100% {
    -webkit-transform:translateX(0%);
            transform:translateX(0%);
    -webkit-text-stroke:3px #fff;
    color: #DBD5C9;
  }
}
@keyframes hello {
  0%{
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
    -webkit-text-stroke:3px #fff;
    color: #DBD5C9;
  }

  100% {
    -webkit-transform:translateX(0%);
            transform:translateX(0%);
    -webkit-text-stroke:3px #fff;
    color: #DBD5C9;
  }
}

#typingsub{
  color: #F93700;
  font-size:9em;
  text-align: start;
  padding: 30px 0 0 540px;
  -webkit-animation: portfolio 2s;
          animation: portfolio 2s;
}
@-webkit-keyframes portfolio{
  0%{
    opacity: 0;
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
  }
  75%{opacity: 0;}
  100%{
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);}
}
@keyframes portfolio{
  0%{
    opacity: 0;
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
  }
  75%{opacity: 0;}
  100%{
    opacity: 1;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);}
}
#typingsub::selection{
  background-color: #fff;
  color: #F93700;
}
#maintext::after{
  content: '';
  display: block;
  width: 0%;
  border-bottom: 1px solid #fff;
  -webkit-animation:line 2s ease-in-out forwards;
          animation:line 2s ease-in-out forwards;
}
@-webkit-keyframes line{
  100%{width: 80%;}
}
@keyframes line{
  100%{width: 80%;}
}


#subtext{
  width: 100%; height: 100%;
  position: absolute;
  right: 0%; top: 25%;
}
#subtext p{
  position: absolute;
  right: 18%; top: 0%;

  text-align: end;
  color: #919191;
  font-size: 0.8em;
  line-height: 1.8;
  padding-bottom: 10px;
}
#subtext .cir{
  position: absolute;
  right: 0; bottom: 0;
  width: 100%;
  z-index: -1;
}
#subtext .cir path{
  stroke-dasharray: 2000px;
  stroke-dashoffset: 2000px;
  -webkit-animation: circle 3s forwards;
          animation: circle 3s forwards;
}
@-webkit-keyframes circle {
  100%{stroke-dashoffset: 0px;}
}
@keyframes circle {
  100%{stroke-dashoffset: 0px;}
}


#foottext{
  width: 70%;
  margin: 0 auto;
  padding: 0;
}
#foottext p{
  color: #919191;
  font-size: 0.8em;
  line-height: 1.8;
}
#foottext h3{
  color: #919191;
