@import url('https://fonts.googleapis.com/css2?family=Source+Serif+Pro:wght@300;400;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&family=Open+Sans&family=Source+Serif+Pro:wght@300;400;600;700;900&display=swap');
*{
    box-sizing: border-box;
    font-family: 'Source Serif Pro', serif, 'Noto Sans KR', sans-serif;
  }
  ::-webkit-scrollbar{width: 5px;}
  ::-webkit-scrollbar-track{background-color: #eee;}
  ::-webkit-scrollbar-thumb{background-color: #F93700;}
  
  a{text-decoration: none;}
  ul,li{margin: 0; list-style: none;}
  html,body{
    margin: 0; padding:0;
    scroll-behavior: smooth;
  }
  h1,h2,h3,h4,p,span{margin: 0;cursor: default}
  html{
    width: 100%;
    position: relative;
  }
  body{
  background-color: #DBD5C9;
  }
  header{
  background-color: #DBD5C9;
  width: 100%;
  position: fixed;
  top: 0;
  border-bottom: 1px solid #fff;
  z-index: 15;
  }
  


  #head{
    width: 85%;
    margin: 0 auto;

    display: flex;
    justify-content: space-between;
  }
  #navlist{
    display: flex;
    padding: 0;
  }
  #navlist li{
    padding: 20px 40px;
  }
  #navlist li.active a{
    text-decoration: line-through solid 2px #ffff;
    color: #fff;
  }
  
  #navlist a{
    color: #F93700;
    font-size:1em;
    font-weight: 800;
  }
  #navlist a:hover{
    text-decoration: line-through solid 2px #ffff;
    color: #fff;
  }
  #navIcon ul{
    display: flex;
    padding: 0;
  }
  #navIcon ul li{
    padding: 18px 15px;
  }
  #navIcon ul li a{
    color: #F93700;
  }
  #navIcon ul li a:hover{
    color:#fff;
  }

  @media screen and (min-width: 768px) and (max-width: 1023px){
  
    #head{
      width: 90%;
    }
    #navlist a{
      font-size:1.2em;
    }
    #navIcon ul li{
      padding: 20px 20px;
    }
  }

  @media screen and (min-width: 360px) and (max-width: 767px){
  
    #head{
      width: 100%;
    }
    #nav{
      width: 80%;
      margin:0 auto;
    }
    #navlist{
      width: 100%;
      justify-content: space-between;
    }
    #navlist li{
      padding: 20px;
    }
    #navlist a{
      color: #F93700;
      font-size:0.8em;
      font-weight: 800;
    }
    #navIcon ul{
      display: none;
    }

  }