@import url('./Common.css');

#about{
    width: 100%; height: 100%;
    margin-top:61px;
    background-color: #fff;
  }
  
#amain{
  width: 100%; height: 100%;
  padding: 100px 0 50px;
  position: relative;
}
#amain::before{
  content: '';
  width: 0%;
  border-bottom: 2px solid #F93700;
  position: absolute;
  right: 0; top:20%;
  animation: aline 1s ease-in-out forwards;
}
@keyframes aline{
  100%{width: 75%;}
}
#amain h1{
  color: #F93700;
  font-size: 5em;
  text-align: end;
  
  position: absolute;
  left:9%; top: 15%;
  transform: rotate(270deg) translateY(100%); 
  transform-origin: 0 100%;
}
#atit{
  width:50%;
  margin-left:35%;
  margin-top: 7%;
  padding-bottom: 100px;
  text-align: end;

  display: flex;
  position: relative;
}

#atxt{
  width: 55%;
  color: #919191;
  padding-top: 230px;
  margin-right:20px;
  
}
#aboutback{
  position: relative;
  width:200px;
  transform: rotate(355deg);
  opacity: 0.5;
}
#atxt h3{
  position: absolute;
  top:43%; left: 30%;
  color: #919191;
  font-size: 1.5em;
  line-height: 1.5;
  z-index: 10;
}
#atxt p{
  padding-top:50px;
  line-height: 1.5;
}

#aimg{
  width: 50%;
}
#aimg img{
    width:350px; height: 400px;
    object-fit: cover;
    overflow: hidden;
}
#asub{
  width: 100%; height: 100%;
  padding: 100px 0;
}

#asubtit{
  position: relative;
  width: 100%;
  overflow: hidden;
  padding: 100px 0;
  text-align: center;
  font-size: 4em;
  line-height: 1.5;
  color: #F93700;
}

#Layer_1{
  position: absolute;
  left: 16%; top:12%;
  width: 50px;
  opacity: 1;
  animation: arrowmove .8s linear infinite;
}
@keyframes arrowmove{
  100%{opacity: 0.4;}
}


#asubtit p:nth-of-type(1){
  color: #DBD5C9;
}
#asubtit h3 a{
  font-size: 1.3em;
  font-weight: 700;
  color: #F93700;
}
#asubtit h3 a::after{
  content: '';
  width: 0%;
  margin-left: 20%;
  display: block;
  border-bottom: 2px solid #F93700;
  transition: .13s;
  animation: linemove 1.8s ease-in infinite;
}
@keyframes linemove{
  100%{width: 60%;}
}
#asubtit h3 a span{
  position: relative;
  letter-spacing: -0.8px;
  display: inline-block;
  animation: movetext 1.8s ease-in-out infinite;
}
@keyframes movetext{
  0%{top: 0;}
  20%{top: -0.2em;}
  40%{top: 0;}
  60%{top: 0;}
  80%{top: 0;}
  100%{top: 0;}
}
#asubtit h3 a span:nth-of-type(1){
  animation-delay: 0.1s;
}
#asubtit h3 a span:nth-of-type(2){
  animation-delay: 0.2s;
}
#asubtit h3 a span:nth-of-type(3){
  animation-delay: 0.3s;
}
#asubtit h3 a span:nth-of-type(4){
  animation-delay: 0.4s;
}
#asubtit h3 a span:nth-of-type(5){
  animation-delay: 0.5s;
}
#asubtit h3 a span:nth-of-type(6){
  animation-delay: 0.6s;
}
#asubtit h3 a span:nth-of-type(7){
  animation-delay: 0.7s;
}
#asubtit h3 a span:nth-of-type(8){
  animation-delay: 0.8s;
}
#asubtit h3 a span:nth-of-type(9){
  animation-delay: 0.9s;
}
#asubtit h3 a span:nth-of-type(10){
  animation-delay: 1s;
}
#asubtit h3 a span:nth-of-type(11){
  animation-delay: 1.1s;
}
#asubtit h3 a span:nth-of-type(12){
  animation-delay: 1.2s;
}
#asubtit h3 a span:nth-of-type(13){
  animation-delay: 1.3s;
}
#asubtit h3 a span:nth-of-type(14){
  animation-delay: 1.4s;
}
#asubtit h3 a span:nth-of-type(15){
  animation-delay: 1.5s;
}
#asubtit h3 a span:nth-of-type(16){
  animation-delay: 1.6s;
}
#asubtit h3 a span:nth-of-type(17){
  animation-delay: 1.7s;
}
#asubtit h3 a span:nth-of-type(18){
  animation-delay: 1.8s;
}
#asubtit h3 a span:nth-of-type(19){
  animation-delay: 1.9s;
}

#asub{
  width: 100%; height:100vh;
  padding: 50px 0;
  margin-top: 50px;
}
#asub_text{
  width: 60%; height: 100%;
  margin: 0 auto;
}
#asub_text ul{
  width: 100%; padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #DBD5C9;
}
#asub_text ul li{
  width: 50%;
  padding: 50px 30px;
  border: 1px dashed #eee;
}
#asub_text ul li p{
  color: #F93700;
  font-size: 1.2em;
  font-weight: 700;
  padding-bottom: 20px;
  text-transform: uppercase;
}
#asub_text ul li p::after{
  content: '';
  display: block;
  width: 100%;
  border-bottom: 2px solid #F93700;
}
#asub_text ul li span{
  font-family: sans-serif;
  color: #fff;
  font-size: 1em;
  line-height: 1.8;
  word-break: keep-all;
}

#asubtwo{
  width: 100%;
  text-align: center;
  overflow: hidden;
  position: relative;
}
#asubskill{
  background-color: #F93700;
}
#asubskill h2{  
  width: 100%;
  padding: 20px 0;
  color: #fff;
  font-size: 1.5em;
  letter-spacing: 3px;
  white-space: nowrap;
  animation: slider 10s linear infinite both;
}
@keyframes slider{
  0%{transform: translateX(0%);}
  100%{transform: translateX(-60%);}
}
#asubpro{
  width: 100%; height: 100%;
  padding: 50px 50px 0;
}

#backCBrain{
  width: 100%; height: 100%;
}
#backCBrain span{
  -webkit-text-stroke:2px #F93700;
  color: #fff;
  font-weight: 800;
  font-size:8em;
}
#asubpro span:hover{
  color: #F93700;
}

