@import url('./css/Common.css');

#main{
  width: 100%; height: 100%;
  margin-top:62px;
  padding: 40px 0;
  overflow: hidden;
  position: relative;
}

#maintext{
  position: relative;
  width: 100%;
  padding: 20px 0;
}
#svgname{
  width:10%;
  position: absolute;
  left:38%; top: 0%;
  animation: namemove 1.2s linear forwards;
}
@keyframes namemove{
  100%{transform: rotate(360deg);}
}
#hello{
  color: #fff;
  font-size:9em;
  margin-left: 200px;
  padding: 20px 0;
  animation: hello 1s;
}
#hello::selection{
  background-color: #F93700;
  color: #fff;
}
@keyframes hello {
  0%{
    transform: translateX(-25%);
    -webkit-text-stroke:3px #fff;
    color: #DBD5C9;
  }

  100% {
    transform:translateX(0%);
    -webkit-text-stroke:3px #fff;
    color: #DBD5C9;
  }
}

#typingsub{
  color: #F93700;
  font-size:9em;
  text-align: start;
  padding: 30px 0 0 540px;
  animation: portfolio 2s;
}
@keyframes portfolio{
  0%{
    opacity: 0;
    transform: translateX(-25%);
  }
  75%{opacity: 0;}
  100%{
    opacity: 1;
    transform: translateX(0%);}
}
#typingsub::selection{
  background-color: #fff;
  color: #F93700;
}
#maintext::after{
  content: '';
  display: block;
  width: 0%;
  border-bottom: 1px solid #fff;
  animation:line 2s ease-in-out forwards;
}
@keyframes line{
  100%{width: 80%;}
}


#subtext{
  width: 100%; height: 100%;
  position: absolute;
  right: 0%; top: 25%;
}
#subtext p{
  position: absolute;
  right: 18%; top: 0%;

  text-align: end;
  color: #919191;
  font-size: 0.8em;
  line-height: 1.8;
  padding-bottom: 10px;
}
#subtext .cir{
  position: absolute;
  right: 0; bottom: 0;
  width: 100%;
  z-index: -1;
}
#subtext .cir path{
  stroke-dasharray: 2000px;
  stroke-dashoffset: 2000px;
  animation: circle 3s forwards;
}
@keyframes circle {
  100%{stroke-dashoffset: 0px;}
}


#foottext{
  width: 70%;
  margin: 0 auto;
  padding: 0;
}
#foottext p{
  color: #919191;
  font-size: 0.8em;
  line-height: 1.8;
}
#foottext h3{
  color: #919191;
  font-size: 0.8em;
  text-align: center;
  margin-top: 20px;
}



@media screen and (min-width: 970px) and (max-width: 1023px){

  #main{
    margin-top:64px;
    padding: 20px 0;
  }
  #svgname{
    width:15%;
    position: absolute;
    left:46%; top: 0%;
    animation: namemove 1.2s linear forwards;
  }
  #hello{
    font-size:8em;
    margin-left: 100px;
    animation: hello 1s;
  }
  #typingsub{
    font-size:8em;
    padding: 30px 0 0 200px;
  }

  #subtext{
    width: 100%; height: 100%;
    position: absolute;
    right: 0%; top: 0%;
  }
  #subtext p{
    position: absolute;
    right: 20%; top: 32%;
    font-size: 0.6em;
  }

  #foottext p{
    font-size: 0.6em;
  }
  #foottext h3{
    font-size: 0.6em;
  }
}

@media screen and (min-width: 768px) and (max-width: 969px){

  #main{
    margin-top:65px;
    padding: 40px 0 0;
  }
  #svgname{
    width:15%;
    position: absolute;
    left:48%; top: 6%;
    animation: namemove 1.2s linear forwards;
  }
  #hello{
    font-size:7em;
    margin-left: 80px;
  }
  #typingsub{
    font-size:6em;
    padding: 30px 0 0 160px;
  }

  #subtext{
    width: 100%; height: 100%;
    position: absolute;
    right: 0%; top: 0%;
  }
  #subtext p{
    position: absolute;
    right: 20%; top: 30%;
    font-size: 0.6em;
  }

  #foottext p{
    font-size: 0.6em;
  }
  #foottext h3{
    font-size: 0.6em;
  }
}

@media screen and (min-width: 360px) and (max-width: 767px){
  #main{
    margin-top:64px;
    padding: 60px 0 20px;
  }
  #maintext{
    position: relative;
    padding: 80px 0 20px;
  }
  #svgname{
    width:30%;
    position: absolute;
    left:52%; top: 20%;
    animation: namemove 1.2s linear forwards;
  }
  #hello{
    font-size:4.4em;
    margin-left: 20px;
  }
  #typingsub{
    font-size:4.2em;
    padding: 30px 0 0 20px;
  }
  #maintext::after{
    content: '';
    display: block;
    width: 0%;
    border-bottom: 1px solid #fff;
    animation:line 2s ease-in-out forwards;
  }
  @keyframes line{
    100%{width: 90%;}
  }
  #subtext{
    width: 100%; height: 100%;
    position: absolute;
    right: 0%; top: 0%;
  }
  #subtext p{
    position: absolute;
    right: 12%; top: 10%;
    font-size: 0.2em;
  }
  #foottext{
    width: 80%;
    margin: 0 auto;
    padding: 0;
  }
  #foottext p{
    font-size: 0.6em;
  }
  #foottext h3{
    margin-top: 60px;
    font-size: 0.6em;
  }
}