@import url('./Common.css');

#listp{
    background-color: #fff;
    width: 100%; height: 100%;
    margin-top: 61px;
}
#listtop{
    width: 100%; height:300px;
    padding: 100px 0 0;
    position: relative;
}
#listtop::before{
    content: '';
    width: 0%;
    border-bottom: 2px solid #F93700;
    position: absolute;
    right: 0; top:51%;
    animation: pline 1s ease-in-out forwards;
  }
  @keyframes pline{
    100%{width: 75%;}
  }
  
#listtop h1{
    color: #F93700;
    font-size: 5em;
    text-align: end;

    position: absolute;
    left: 9%; top: 60%;
    transform: rotate(270deg) translateY(100%);
    transform-origin: 0 100%;
}

#listcon{
    width: 70%; height: 100%;
    padding: 0;
    margin-left:23%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
#listcon li{
    width: 48%; height: 100%;
    padding: 10px 30px;    
    overflow: hidden;
    margin-bottom: 50px;
}

#listcon li h3::before{
    content: '';
    display: inline-block;
    width: 100%;
    border-top: 1px solid #F93700;
    border-bottom: 1px solid #F93700;
}
#listcon li h3{
    color: #F93700;
    font-size: 1.2em;
    text-transform: uppercase;
}
#listcon li h3::after{
    content: '';
    display: inline-block;
    width: 100%;
    border-top: 1px solid #F93700;
    border-bottom: 1px solid #F93700;
}

#listcon li figure{
    margin:10px 0;
    width: 100%; height: 300px;
    overflow: hidden;
}
#listcon li figure p{
    background-color: #DBD5C9;
    height: 100%;
    color: #fff;
    font-size: 12em;

    display: flex;
    justify-content: center;
    align-items: center;
}
#listcon li figure:hover p{
    display: none;
}
#listcon li figure video{
    width: 100%; height: 100%;
    object-fit: cover;
    overflow: hidden;
}
#listcon div{
    width: 100%;
    word-break: keep-all;
    padding: 15px 0;
}
#listcon div::before{
    content: '';
    display: block;
    margin: 0 auto;
    width: 100%;
    border-top: 1px dashed floralwhite;
    padding-top: 10px;
}
#listcon div span{
    font-family: sans-serif;
    color: #919191;
    font-size: 0.8em;
    line-height: 1.6;
}



#listcon li:nth-of-type(2n){
    padding-top: 200px;
}
#listcon li:nth-of-type(2n) h3:before{
    content: '';
    display: inline-block;
    width: 100%;
    border-top: 1px solid #DBD5C9;
    border-bottom: 1px solid #DBD5C9;
}
#listcon li:nth-of-type(2n) h3::after{
    content: '';
    display: inline-block;
    width: 100%;
    border-top: 1px solid #DBD5C9;
    border-bottom: 1px solid #DBD5C9;
}
#listcon li:nth-of-type(2n) figure p{
    background-color: #F93700;
    color: #fff;
}
#listcon li:nth-of-type(2n) figure p:hover{
    display: none;
}



@media screen and (min-width: 970px) and (max-width: 1023px){

    #listp{
        margin-top: 65px;
    }

    #listtop::before{
        position: absolute;
        right: 0; top:59%;
        animation: pline 1s ease-in-out forwards;
    }
    @keyframes pline{
        100%{width: 70%;}
    }
    #listtop h1{
        position: absolute;
        left: 9%; top: 75%;
    }
    #listcon{
        margin-left:28%;
    }
    #listcon li{
        padding: 10px;
        margin-bottom: 0px;
    }
    #listcon li h3{
        font-size: 1.6em;
    }
    #listcon li:nth-of-type(2n){
        padding-top: 180px;
    }
    #listcon div{
        width: 100%;
        word-break: keep-all;
        padding: 15px 10px;
    }
    
}

    



@media screen and (min-width: 768px) and (max-width: 969px){

    #listtop::before{
        position: absolute;
        right: 0; top:43%;
        animation: pline 1s ease-in-out forwards;
    }
    @keyframes pline{
       100%{width: 65%;}
    }
    #listtop h1{
        position: absolute;
        left: 9%; top: 54%;
        font-size: 4em;
    }
    #listcon{
        width: 80%;
        margin-left:20%;
    }
    #listcon li{
        padding: 10px;
        margin-bottom: 0px;
    }
    #listcon li:nth-of-type(2n){
        padding-top: 150px;
    }
}

@media screen and (min-width: 360px) and (max-width: 767px){
    #listtop{
        height:250px;
        padding: 50px 0 0;
    }
    #listtop::before{
        position: absolute;
        right: 0; top:39%;
        animation: pline 1s ease-in-out forwards;
    }
    @keyframes pline{
        100%{width: 50%;}
    }
    #listtop h1{
        position: absolute;
        left: 14%; top: 45%;
        font-size: 2.5em;
    }
    #listcon{
        width: 80%;
        margin-left:20%;
        display: block;
        padding-bottom: 50px;
    }
    #listcon li{
        width: 100%;
        padding: 10px 30px;    
        overflow: hidden;
        margin-bottom: 50px;
    }
    #listcon li:nth-of-type(2n){
        padding-top: 0px;
    }
    #listcon div{
        width: 100%;
        word-break: keep-all;
        padding: 15px 10px;
    }
    #listcon div span{
        font-family: sans-serif;
        color: #919191;
        font-size: 0.7em;
        line-height: 1.6;
    }
}