@import url('./Common.css');


#intro{
    width: 100%; height: 100%;
    margin-top: 61px;
}
#intro_tit{
    width: 100%; height: 650px;
    position: relative;
    overflow: hidden;
}
#intro_tit::before{
    content: '';
    width: 0%;
    border-bottom: 2px solid #F93700;
    position: absolute;
    right: 0; top:23%;
    animation: iline 1s forwards;
    animation-delay: .8s;
}
@keyframes iline{
    100%{width: 75%;}
}
#intro_tit h1{
    color: #F93700;
    font-size: 5em;
    text-align: end;

    position: absolute;
    left:9%; top:46%;
    transform: rotate(270deg) translateY(100%); 
    transform-origin: 0 100%
}
#intro_tit div{
    width: 60%;
    text-align: end;
    padding: 50px 50px 0;
    margin-top: 15%;
    margin-left: 32%;
    line-height: 1.4;
}
#intro_tit div p{
    display: block;
    font-size: 2em;
    color: #fff;
    transform: translateX(100%);
    animation: intromove 1.2s forwards;
    animation-delay: .8s;
}
@keyframes intromove{
    50%{transform: scale(1.1);}
    100%{transform: translateX(0%);}
}

#intro_tit div p b{
    font-size: 2.1em;
    color: #F93700;
}

.accordion{
    width: 80%;
    padding: 100px 50px;
    margin: 50px auto;
}
.que{
    position: relative;
    padding: 15px 0;
    font-size: 1.4em;
    color: #F93700;
    font-weight: 700;
    border-bottom: 2px dashed #fff;
    margin-bottom: 30px;
}
.que::before{
    content: 'Q';
    display: inline-block;
    margin-right: 15px;
}
.anw{
    padding: 20px 40px 50px;
    /* display: none; */
}
.anw.active{
    display: block;
}
.anw span{
    font-family: sans-serif;
    line-height: 1.8;
    color: #919191;
    font-size: 1em;
    word-break: keep-all;
}
.anw::before{
    content: 'A';
    display: inline-block;
    font-size: 1.6em;
    font-weight: 700;
    margin-right: 15px;
    color: #919191;
}

@media screen and (min-width: 970px) and (max-width: 1023px){
    #intro{
        margin-top: 65px;
    }
    #intro_tit::before{
        content: '';
        width: 0%;
        border-bottom: 2px solid #F93700;
        position: absolute;
        right: 0; top:27%;
        animation: iline 1s forwards;
        animation-delay: .8s;
    }
    @keyframes iline{
        100%{width: 70%;}
    }
    #intro_tit h1{
        position: absolute;
        left:9%; top:56%;
    }
    #intro_tit div{
        margin-top: 20%;
    }
    #intro_tit div p{
        font-size: 1.4em;
    }
    .accordion{
        width: 90%;
        padding: 50px;
        margin: 0 auto;
    }
    .anw span{
        font-family: sans-serif;
        line-height: 1.8;
        color: #919191;
        font-size: 0.8em;
        word-break: keep-all;
    }

}


